@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    background-color: #f5f5f5;
}

.generator {
    width: 100%;
    padding: 20px;
    border: 1px solid #dbdbdb;
    display: flex;
    justify-content: center;
    margin: 0px auto 0 auto;
    background-color: #f5f5f5;
    border-bottom: 0;
}

.generator__inputs {
    width: 100%;
    margin-right: 20px;
}

.input-group {
    margin-bottom: 15px;
}

.input-group h4 {
    color: #363636;
    font-size: 1rem;
    margin: 0 0 12px 0;
}

.input-group__input {
    outline: none;
    color: #363636;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #b5b5b5;
    margin-bottom: 15px;
}

.generator__result-wrapper {
    max-width: 500px;
    min-width: 500px;
    min-height: 500px;
    margin: -21px -21px -20px auto;
    border: 1px solid #dbdbdb;
    border-bottom: 0;
    background-color: #ededed;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    overflow: hidden;
}

.generator__output {
    margin: 0 auto 0 auto;
    width: 100%;
    padding: 20px;
    font-size: 20px;
    font-weight: 400;
    background-color: #4a4a4a;
    color: #fff;
}

.btn-copy {
    border: 0;
    margin: 0 auto 0px auto;
    display: block;
    padding: 20px;
    width: 100%;
    font-size: 2rem;
    cursor: pointer;
    background-color: #3abb67;
    color: #fff;
}

.btn-copy:hover {
    background-color: #3ec46d;
}

.transform-box {
    width: 200px;
    height: 200px;
    background-color: red;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 2rem;
    font-weight: 700;
}

.filter-box {
    background: url("https://www.gardendesign.com/pictures/images/675x529Max/site_3/helianthus-yellow-flower-pixabay_11863.jpg");
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center;
}

@media (max-width: 1200px) {
    .generator__result-wrapper {
        max-width: 400px;
        min-width: 400px;
        min-height: 400px;
    }
}

@media (max-width: 1000px) {
    .generator {
        flex-direction: column;
    }
    .generator__result-wrapper {
        max-width: calc(100% + 42px);
        min-width: calc(100% + 42px);
        min-height: 400px;
        height: 400px;
        margin: 20px 0 -20px -21px;
    }
    .btn-copy {
        font-size: 1.8rem;
    }
}

@media (max-width: 360px) {
    .generator__result-wrapper {
        min-height: 300px;
        height: 300px;
    }
    .transform-box {
        width: 150px;
        height: 150px;
        font-size: 1.5rem;
    }
    .btn-copy {
        font-size: 1.4rem;
    }
}
